/** @augments {PureComponent<{ authorized: PureComponent, else: PureComponent }>} */
class Protected extends PureComponent {
    render() {
        if (!client.ready)
            return <></>;
        else
            return <Routes>
                {Pages.requireAuthorization.map((path, index) => <Route key={index + path} path={path} element={<RequireAuthorization>{this.props.authorized}</RequireAuthorization>} />)}
                <Route key="@Else" path="*" element={this.props.else} />
            </Routes>
    }
}

export { Protected/*, Authorized, NotAuthorized, RequireAuthorization*/ };