import React, { lazy, Suspense } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';

import "./Content.css";
import sharedMemory from './CLASSES/SharedMemory';

const Home = lazy(() => import('./Home'));
const Login = lazy(() => import('./Login'));
const Register = lazy(() => import('./Register'));
const Village = lazy(() => import("./Village"));
const Center = lazy(() => import("./Center"));
const Building = lazy(() => import("./Building"));
const Map = lazy(() => import("./Map"));
const View = lazy(() => import("./View"));
const Profile = lazy(() => import("./Profile"));
const Messages = lazy(() => import("./Messages"));

/**@param {{ ready: Boolean, authorized: Boolean }} props */
function Content(props) {
    return <div className="content" key={useLocation().key}>
        <Suspense fallback={<ViewProgressBar />}>
            <Routes>
                <Route path="/" element={<Home {...props} />} />
                <Route path="/Home" element={<Home {...props} />} />
                <Route path="/Login" element={<Login {...props} />} />
                <Route path="/Register" element={<Register {...props} />} />
                <Route path="/Rules" element={"التعليمات"} />
                {/* Auth */}
                <Route path="/Village" element={<Village register="village" {...props} />} />
                <Route path="/Center" element={<Center register="center" {...props} />} />
                <Route path="/Map" element={<Map register="map" {...props} />} />
                <Route path="/Statistics" element={"الاحصائيات"} />
                <Route path="/Reports" element={"التقارير"} />
                <Route path="/Messages" element={<Messages display="inbox" {...props} />} />
                <Route path="/Messages/view" element={<Messages display="view" {...props} />} />
                <Route path="/Messages/send" element={<Messages display="send" {...props} />} />
                <Route path="/Messages/sent" element={<Messages display="sent" {...props} />} />
                <Route path="/Messages/note" element={<Messages display="note" {...props} />} />
                <Route path="/Building" element={<Building {...props} />} />
                <Route path="/View" element={<View {...props} />} />
                <Route path="/Overview" element={"نظرة عامة على القرى"} />
                <Route path="/Profile" element={<Profile display="profile" {...props} />} />
                <Route path="/Profile/edit" element={<Profile display="edit" {...props} />} />
                <Route path="*" element={<><br /><h3 className="NotFound">الصفحة التي طلبتها غير موجودة</h3></>} />
            </Routes>
        </Suspense>
    </div>;
}

class ViewProgressBar extends React.PureComponent {
    componentDidMount() {
        sharedMemory.safe("progress-bar", progress => { progress.show(); });
    }
    componentWillUnmount() {
        sharedMemory.safe("progress-bar", progress => { progress.hide(); });
    }
    render() {
        return <></>;
    }
}

export default Content;