import ImageMap from "../Images";

const TITLE = {
    res: {
        r1: "الخشب",
        r2: "الطين",
        r3: "الحديد",
        r4: "القمح",
        r5: "استهلاك القمح",
    }
}

const IMG = {
    res(className = "") {
        let t = TITLE.res[className];
        return <img width={16} height={16} src={ImageMap.pixel} alt={t} title={t} className={className} />;
    },
    clock() {
        return <img src={ImageMap.pixel} alt="مدة البناء" title="مدة البناء" className="clock" />;
    },
    warehouse() {
        return <img src={ImageMap.pixel} alt="المخزن" title="المخزن" className="warehouse" />;
    },
    granary() {
        return <img src={ImageMap.pixel} alt="مخزن الحبوب" title="مخزن الحبوب" className="granary" />;
    }
}

export { IMG, TITLE };