import React from 'react';
import { Link } from "react-router-dom";
import { Protected } from './AuthorizedContent';
import { OpenGuid } from "./Guid";
import Pure from './Pure';
import "./SideNavi.css";

class SideNavi extends Pure {
    render() {
        return <>
            <div className="side_navi">
                <p>
                    <Protected
                        authorized={<>
                            <Link to="/Home">الصفحة الرئيسية</Link>
                            <br />
                            <Link to="#" onClick={OpenGuid()}>الدليل السريع</Link>
                            <Link to="/Profile">ملف التعريف</Link>
                            {/* <Link to="/Rules">التعليمات</Link> */}
                            <br />
                            <Link to="/Login"><b>تسجيل الخروج</b></Link>
                        </>}
                        else={<>
                            <Link to="/Home">الصفحة الرئيسية</Link>
                            <br />
                            <Link to="/Login"><b>تسجيل الدخول</b></Link>
                            <Link to="/Register"><b>تسجيل جديد</b></Link>
                            <br />
                            <Link to="#" onClick={OpenGuid()}>الدليل السريع</Link>
                            <Link to="/Rules">التعليمات</Link>
                        </>}
                    />
                </p>
            </div>
        </>;
    }
}

export default SideNavi;