import { PureComponent } from "react";
import eventManager from "./CLASSES/EventManager";

class LocationObserver extends PureComponent {
    updates = 0;
    constructor() {
        super();
        eventManager.create("LocationUpdated");

    }
    componentDidMount() {
        if (this.updates === 0) return;
        this.updates++;
        eventManager.trigger("LocationUpdated");
        console.log("LocationUpdated", this.updates);
    }
    render() {
        return <></>;
    }
}

export default (LocationObserver);