class EventManager {
    constructor() {
        this.events = {};
        this.fn = {};
    }
    create(name = "") {
        if (this.events[name] != null) return;
        this.events[name] = new window.Event(name);
        this.fn[name] = {};
        window.addEventListener(name, () => this.run(name));
    }
    trigger(name = "") {
        window.dispatchEvent(this.events[name]);
    }
    waitFor(name = "", then = () => { }, once = true) {
        return new Promise(resolve => {
            window.addEventListener(name, () => {
                resolve(then());
            }, { once });
        });
    }
    run(name = "") {
        let fn = this.fn[name];
        if (typeof fn !== "undefined")
            Object.values(fn).forEach(f => f());
    }
    listen(name = "", id = "listener", fn = () => { }) {
        if (typeof this.fn[name] !== "undefined")
            this.fn[name][id] = fn;
    }
    unListen(name = "", id = "") {
        if (typeof this.fn[name] !== "undefined")
            delete this.fn[name][id];
    }
}

const eventManager = new EventManager();

export default eventManager;