import React from 'react';
import './App.css';

import Header from './components/Header';
import SideNavi from './components/SideNavi';
import Content from './components/Content';
import SideInfo from './components/SideInfo';
import Footer from './components/Footer';

import { Container, Row, Col } from "react-bootstrap";
import Pure from './components/Pure';
import client from './components/CLASSES/client';
import socket from './components/CLASSES/Socket';
import { ProgressBar } from "./components/SubComponents/ProgressBar";
import { DialogBox } from "./components/SubComponents/Modal";
import { Guid } from './components/Guid';
import { SnackBar } from './components/Interfaces/SnackBar';
import { Protected } from './components/AuthorizedContent';
import ResourceBar from './components/ResourceBar';
import requestJoin from './components/API/Join';

class App extends Pure {
  state = {
    small: window.innerWidth < 960,
    visible: false,
    connected: false,
    ready: false,
    authorized: false,
    naviOpened: false,
    sideOpened: false,
  }
  constructor(props) {
    super(props);
    window.addEventListener("load", () => { this.updateVisibility(); });
    window.addEventListener("scroll", () => { this.updateVisibility(); });
    window.addEventListener("resize", () => {
      if (!this.componentIsReady) return;
      let w = window.innerWidth;
      let s = this.state.small;
      if (w <= 960 && !s)
        this.setState({ small: true });
      else if (w > 960 && s)
        this.setState({ small: false });
    });

    socket.connect();
    requestJoin().then(response => {
      if (!this.componentIsReady) return;
      client.join(response);
      this.setState({ ready: true, authorized: response.authorized });
    });
  }

  get isAtTheBottom() {
    return (window.innerHeight + window.scrollY) >= document.body.clientHeight - 20;
  }

  updateVisibility() {
    if (!this.componentIsReady) return;
    let v = this.state.visible;
    let b = this.isAtTheBottom;
    if (v && !b)
      this.setState({ visible: false });
    else if (!v && b)
      this.setState({ visible: true });
  }
  render() {
    let mobile = this.state.small;
    let props = {
      ready: this.state.ready,
      authorized: this.state.authorized,
      mobile
    };
    let openNavi = () => this.updateState({ naviOpened: !this.state.naviOpened, sideOpened: false });
    let openSide = () => this.updateState({ naviOpened: false, sideOpened: !this.state.sideOpened });
    let hideBoth = () => this.updateState({ naviOpened: false, sideOpened: false });
    let naviIsOpened = mobile && this.state.naviOpened;
    let sideIsOpened = mobile && this.state.sideOpened;
    return <>
      <Container className="not-from-work" fluid>
        <Row>
          <Col aria-colspan={3} onClick={hideBoth}>
            <Header {...props} />
          </Col>
        </Row>
        <Row className="middle-row">
          <Col className="navi-col" style={{ width: naviIsOpened ? "50%" : "" }}><SideNavi {...props} /></Col>
          <span className="navi-drawer" onClick={openNavi} style={{ right: naviIsOpened ? "50%" : "" }}>{"<"}</span>
          <Col className="middle-col" style={{ minWidth: mobile ? 0 : 650 }} onClick={hideBoth}>
            <ProgressBar register="progress-bar" />
            <DialogBox name="guid" title="الدليل السريع"><Guid /></DialogBox>
            <SnackBar register="snackbar" />
            <Protected authorized={<ResourceBar />} else={<></>} />
            <Content {...props} />
          </Col>
          <Col className={mobile ? "side-col drawer-col" : "side-col"} style={{ width: sideIsOpened ? "75%" : "" }}><SideInfo {...props} /></Col>
          <span className="side-drawer" onClick={openSide} style={{ left: sideIsOpened ? "75%" : "" }}>{">"}</span>
        </Row>
        <Row className="footer-row fixed-bottom" style={{ visibility: this.state.visible ? "visible" : "hidden" }}>
          <Col aria-colspan={3}>
            <Footer />
          </Col>
        </Row>
      </Container>
    </>;
  }
}

export default App;
