import Pure from "./Pure";
import { IMG } from "./SubComponents/IMG";
import "./ResourceBar.scss";
import getResources from "./API/Resources";

class ResourceBar extends Pure {
    state = {
        ready: false
    }
    onReady() {
        getResources().then(response => {
            if (!this.componentIsReady) return;
            this.resources = response;
            this.setState({ ready: true });
        });
    }
    render() {
        if (!this.state.ready)
            return <></>;
        let res = this.resources, prod = res.producation;
        return <div id="resources">
            <div>
                <label className="warehouse">{IMG.warehouse()}<span>{res.warehouse}</span></label>
                <label>{IMG.res("r1")}<span title={prod[0]}>{res.resources[0]}</span></label>
                <label>{IMG.res("r2")}<span title={prod[1]}>{res.resources[1]}</span></label>
                <label>{IMG.res("r3")}<span title={prod[2]}>{res.resources[2]}</span></label>
                <label className="granary">{IMG.granary()}<span>{res.granary}</span></label>
                <label>{IMG.res("r4")}<span title={prod[3]}>{res.resources[3]}</span></label>
                <label>{IMG.res("r5")}<span>{res.consumption}</span></label>
            </div>
        </div>;
    }
}

export default ResourceBar;