//import Pure from "../Pure";

class SharedMemory {
    /** @type {Pure[]} */
    memory = {};
    register(name = "", element) {
        this.memory[name] = element;
    }
    remove(name = "") {
        delete this.memory[name];
    }
    /** @returns {Pure} */
    get(name = "") {
        let m = this.memory[name];
        if (typeof m === "undefined")
            return null;
        else
            return m;
    }
    updateState(name, data = {}) {
        this.safe(name, e => e.updateState(data));
        return this;
    }
    updateProps(name, props = {}, silent = false) {
        this.safe(name, e => e.updateProps(props, silent));
        return this;
    }
    /** @param {function(Pure): void} fn */
    safe(name, fn) {
        let e = this.get(name);
        if (e === null) return;
        fn(e);
    }
}

const sharedMemory = new SharedMemory();

export default sharedMemory;