import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class PlayerVillageList {
    /** @type {{id: Number, name: String, x: Number, y: Number}[]} */
    villages = []
    /** @param {[Number, String, Number, Number][]} response */
    constructor(response = []) {
        if (!Array.isArray(response)) return;
        this.villages = response[0].map(v => ({ id: v[0], name: v[1], x: v[2], y: v[3] }));
    }
}

/**@returns {Promise<PlayerVillageList>} */
function listPlayerVillages() { return socket.request(codes.LIST_PLAYER_VILLAGES, {}); }
socket.defineStructure(codes.LIST_PLAYER_VILLAGES, PlayerVillageList);

export default listPlayerVillages;