import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class JoinResponse {
    /** @param {[String, Number, Boolean, Number]} response */
    constructor(response) {
        this.uid = response[0];
        this.server = response[1];
        this.authorized = response[2] === 1;
        this.activeVillageId = response[3];
    }
}

/** @returns {Promise<JoinResponse} */
export default function requestJoin() { return socket.request(codes.JOIN, {}); }
socket.defineStructure(codes.JOIN, JoinResponse);
