import listPlayerVillages from "./API/PlayerVillageList";
import Pure from "./Pure";
import { Link } from "react-router-dom";

export default class VillageList extends Pure {
    state = {
        count: 0,
        villages: <></>
    }
    onReady() {
        listPlayerVillages().then(list => {
            let villages = list.villages.map(village => <tr key={village.id}>
                <td>●</td>
                <td><Link to={"?vid=" + village.id}>{village.name}</Link></td>
                <td>({village.x} , {village.y})</td>
            </tr>);
            this.updateState({ count: list.villages.length, villages })
        })
    }
    render() {
        return <table id="vlist">
            <thead><tr><th colSpan={4}><Link to="/Overview">خريطة القرى ({this.state.count}):</Link></th></tr></thead>
            <tbody>{this.state.villages}</tbody>
        </table>;
    }
}