import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class ResourceResponse {
    constructor(response = []) {
        if (!Array.isArray(response)) return;
        /** @type {Number[]} */
        this.resources = response[0];
        /** @type {Number[]} */
        this.producation = response[1];
        /** @type {Number} */
        this.consumption = response[2];
        /** @type {Number} */
        this.warehouse = response[3];
        /** @type {Number} */
        this.granary = response[4];
    }
}

/**@returns {Promise<ResourceResponse>} */
function getResources() { return socket.request(codes.GET_RESOURCES, {}); }
socket.defineStructure(codes.GET_RESOURCES, ResourceResponse);

export default getResources;