import { Navigate } from "react-router";
import { Pages } from "../StaticData";
import client from "./CLASSES/client";
import eventManager from "./CLASSES/EventManager";
import sharedMemory from "./CLASSES/SharedMemory";
import Pure from "./Pure";

class Redirect extends Pure {
    state = {
        url: "",
        apply: false
    }
    onReady() {
        eventManager.listen("authorization_updated", "redirect", () => {
            if (!client.authorized && Pages.requireAuthorization.includes(window.location.pathname)) {
                this.updateState({ url: "/Login", apply: true });
            }
        })
    }
    navigate() {
        if (this.state.apply) {
            this.updateState({ apply: false });
            return <Navigate to={this.state.url} />;
        }
        return <></>;
    }
    render() {
        return this.navigate();
    }
}

function redirectTo(url = "") {
    sharedMemory.updateState("Redirect", { url, apply: true });
}

export { Redirect, redirectTo };