const Pages = {
    requireAuthorization: [
        "/Village",
        "/Center",
        "/Map",
        "/Statistics",
        "/Reports",
        "/Messages",
        "/Messages/view",
        "/Messages/send",
        "/Messages/sent",
        "/Messages/note",
        "/Building",
        "/View",
        "/Overview",
        "/Profile",
        "/Profile/edit"
    ],
    NoAuthorization: [
        "/",
        "/Home",
        "/Login",
        "/Register",
        "/Rules",
        "/NotFound"
    ],
    get hasAuthorizationRequired() {
        return this.requireAuthorization.indexOf(window.location.pathname) !== -1;
    }
}

export { Pages };