import React from 'react';
import { Link } from "react-router-dom";
import Pure from './Pure';
import {
    Navbar, Nav,
    Button, OverlayTrigger, Tooltip
} from 'react-bootstrap';
import Signal from '@material-ui/icons/SignalCellularAlt';
import client from './CLASSES/client';
import ImageMap from './Images';
import socket from './CLASSES/Socket';
import "./Header.css";
import { Protected } from './AuthorizedContent';


class Header extends React.PureComponent {
    render() {
        return <div className="header">
            <div className="top_header"><div className="game_header" alt="header"></div></div>
            <Navbar variant="light" className="t-navbar">
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mx-auto">
                        <div className="header-items">
                            <Protected
                                authorized={<>
                                    <ServerNumber />
                                    <PlayerIcons />
                                </>}
                                else={<>
                                    <Link className="nav-link" to="/Home"><Button variant={"outline-dark"}>الرئيسية</Button></Link>
                                    <Link className="nav-link" to="/Login"><Button variant={"outline-dark"}>الدخول</Button></Link>
                                    <Link className="nav-link" to="/Register"><Button variant={"outline-dark"}>التسجيل</Button></Link>
                                    <Link className="nav-link" to="/Rules"><Button variant={"outline-dark"}>التعليمات</Button></Link>
                                </>}
                            />
                            <span className="server-time">
                                <Signal fontSize="small" htmlColor="#888888" />
                                <ServerResponseTime />
                            </span>
                        </div>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    }
}

function ServerNumber() {
    return <span className="server-number" dir="rtl">السيرفر {client.serverNumber}</span>;
}

class ServerResponseTime extends Pure {
    state = { responseTime: 0 };
    onReady() {
        socket.onPong = () => this.updateState({ responseTime: Math.floor(socket.responseTime) });
    }
    render() {
        return <span>{this.state.responseTime}ms</span>
    }
}

class PlayerIcons extends Pure {
    state = { update: 0 };
    update() {
        this.updateState({ update: Date.now() });
    }
    render() {
        let images = ImageMap.header;
        return <>
            <NavigationImage onClick={() => this.update()} href="/Village" id="1" title="خريطة القرية" src={images.villageMap} />
            <NavigationImage onClick={() => this.update()} href="/Center" id="2" title="مركز القرية" src={images.villageCenter} />
            <NavigationImage onClick={() => this.update()} href="/Map" id="3" title="الخريطة" src={images.map} />
            <NavigationImage onClick={() => this.update()} href="/Statistics" id="4" title="الاحصائيات" src={images.stats} />
            <svg viewBox="0 0 80 80" className="header-inbox">
                <image className="header-images" xlinkHref={images.inbox0} x="0" y="0" height="80" width="80" />
                <Link to="/Reports" onClick={() => this.update()}>
                    <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-5"}>التقارير</Tooltip>}>
                        <polygon opacity={0} points="39,5 39,21 40,47 44,56 49,59 55,58 53,74 64,67 73,55 76,38 72,23 60,11 46,5 39,5" />
                    </OverlayTrigger>
                </Link>
                <Link to="/Messages" onClick={() => this.update()}>
                    <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-6"}>الرسائل</Tooltip>}>
                        <polygon opacity={0} points="22,8 8,25 5,39 7,52 15,66 31,75 49,75 59,70 53,60 45,57 40,48 39,40 39,28 37,21 37,5" />
                    </OverlayTrigger>
                </Link>
            </svg>
        </>;
    }
}

/** @augments {Pure < { href: String, id: String, title: String, src: String, data: String, onClick: function (): void } >} */
class NavigationImage extends Pure {
    render() {
        return <Link to={this.props.href} onClick={() => this.props.onClick()}>
            <OverlayTrigger placement="top" overlay={<Tooltip id={"nav-tooltip-" + this.props.id}>{this.props.title}</Tooltip>}>
                <img width={80} height={80} src={this.props.src} alt={this.props.title} className={"header-images" + (window.location.pathname === this.props.href ? " active" : "")} />
            </OverlayTrigger>
        </Link>
    }
}

export default Header;