import Log from "../Functions/Log";
import client from "./client";
import eventManager from "./EventManager";
import Coder from "../Functions/coders";

const DEV = process.env.NODE_ENV === "development";

// const HOST = "ws://localhost";
let SOCKET_ADDRESS = "wss://naif-eu.herokuapp.com";
if (DEV)
    SOCKET_ADDRESS = "ws://192.168.1.2:4096";
// SOCKET_ADDRESS = "ws://localhost:4096";
// SOCKET_ADDRESS = "ws://10.168.32.243:4096";
// SOCKET_ADDRESS = "wss://naif-eu.herokuapp.com";


let responseTime = 0;

class Socket {
    /** @type {WebSocket} */
    ws = null;
    ready = false;
    pendings = {};
    structures = {};
    /** @type {(function(): void)[]} */
    waiting = [];
    interval = -1;
    responseTime = 0;
    cache = {};
    // response = [];
    // onReady = () => { };
    onPong = () => { };

    constructor() {
        eventManager.create("authorization_updated");
    }


    defineStructure(opcode = 0, structure = {}) {
        this.structures[opcode] = structure;
    }

    connect() {
        if (this.ws !== null) {
            console.log("Reset..");
            return this.close();
        }
        let ws = this.ws = new WebSocket(SOCKET_ADDRESS);
        ws.onopen = () => { this.handleOpen(); }
        ws.onmessage = m => { this.handleMessage(m.data); }
        ws.onclose = e => { this.handleClose(e.reason); }
        ws.onerror = e => { this.handleError(e.message); }
    }
    handleOpen() {
        this.ready = true;
        this.waiting.forEach(fn => fn());
        this.waiting = [];
        this.ping();
    }
    /** @param {Blob} bytes */
    async handleMessage(bytes) {
        if (bytes === "0") {
            this.responseTime = performance.now() - responseTime;
            this.onPong();
            setTimeout(() => this.ping(), DEV ? 10000 : 1000);
            return;
        }
        if (!(bytes instanceof Blob)) return;
        let data = new Uint8Array(await bytes.arrayBuffer());
        let opcode = data[0];
        let fn = this.pendings[opcode];
        if (typeof fn === "function") {
            let response = Coder.Decode(data.slice(1));
            let ok = response !== null;
            if (ok && typeof response.authorized === "boolean") {
                let updated = client.authorized !== response.authorized;
                client.authorized = response.authorized;
                if (updated) eventManager.trigger("authorization_updated");
            }
            let e = new this.structures[opcode](response.data || response);
            console.dir(e);
            fn(e);
        }
    }
    handleClose(reason = "") {
        this.ready = false;
        this.ws = null;
        this.retry = 3;
        Log(`Socket is closed: Reason[${reason}]\nReconnect will be attempted in ${this.retry} seconds.`);
        setTimeout(() => this.connect(), this.retry * 1000);
    }
    handleError(err = "") {
        this.ready = false;
        Log(`Socket encountered error: ${err}`);
        this.close();
    }
    close() {
        if (this.ws !== null) {
            this.ready = false;
            this.ws.close(1000, "[Just closing]");
            this.ws = null;
        }
    }
    /** @param {Uint8Array} bytes */
    send(bytes) {
        if (this.ready && this.ws !== null && ArrayBuffer.isView(bytes) && bytes.length <= 4096)
            this.ws.send(bytes);
    }
    /** @param {function({}): void} hasCache * @param {function({}): void} hasUpdated * @returns {Promise<{}>} */
    async cached(opcode = 0, data = {}, hasCache, hasUpdated) {
        if (typeof this.cache[opcode] !== "undefined" && typeof hasCache === "function") {
            console.time(`Cache[${opcode}]`);
            hasCache(this.cache[opcode]);
            console.timeEnd(`Cache[${opcode}]`);
        }
        console.time(`Response[${opcode}]`);
        console.log([this.ready, this.ws]);
        return this.request(opcode, data).then(response => {
            console.timeEnd(`Response[${opcode}]`);
            hasUpdated(response);
        });
    }
    /** @returns {Promise<{}>} */
    request(opcode = 0, data = []) {
        return new Promise(resolve => {
            let bytes = this.Package(opcode, data);
            if (bytes.length < 4096) {
                this.pendings[opcode] = resolve;
                this.onReady(() => this.ws.send(bytes));
            }
            else {
                if (DEV)
                    console.error("ERROR: MESSAGE[" + opcode + "]_IS_TOO_BIG.");
                resolve(null);
            }
        });
    }

    ping() {
        if (!this.ready || this.ws === null) return;
        responseTime = performance.now();
        this.ws.send(0);
    }
    Package(opcode, data) {
        let content = Coder.Encode({ uid: client.uid, key: client.key, data });
        let bytes = new Uint8Array(content.length + 1);
        bytes[0] = opcode;
        bytes.set(content, 1);
        return bytes;
    }
    /** @param {function(): void} fn */
    onReady(fn) {
        if (!this.ready || this.ws === null) {
            this.waiting.push(fn);
        } else {
            fn();
        }
    }
}

let socket = new Socket();


// let float = (a, b = 9) => ~~(a * (10 ** b)) / (10 ** b);


export default socket;