import React from 'react';
import { Protected } from './AuthorizedContent';
import Pure from './Pure';
//import { Link } from "react-router-dom"
import VillageList from './VillageList';
import "./SideInfo.scss";

class SideInfo extends Pure {
    render() {
        return <div id="side_info">
            <Protected
                authorized={<VillageList />}
                else={
                    <article className="news">
                        <h2>مميزات اللعبة</h2>
                        <ul>
                            <li>مجاناً <b>5000 ذهب</b> عند التسجيل</li>
                            <li>حماية مدة <b>24 ساعة</b> عند التسجيل</li>
                            <li>الذهب المشترى سيكون مرتبط بالإيميل المسجل ويمكن إستخدامه في سيرفرات أخرى أو في جولات أخرى <label><b>(حقك محفوظ)</b></label></li>
                            <li>إمكانية تحويل الذهب المشترى بين الحسابات</li>
                            <li>تستطيع إرسال ذهب كـ هدية إلى لاعب معين</li>
                        </ul>
                    </article>
                }
            />
        </div>;
    }
}

export default SideInfo;