let TE = new TextEncoder();
let DE = new TextDecoder();

const Coder = {
    Encode(data = {}) {
        return TE.encode(JSON.stringify(data)).map((byte, offset) => byte - (offset + 1));
    },
    /** @param {Uint8Array} bytes @returns {{}} */
    Decode(bytes) {
        return JSON.parse(DE.decode(bytes));
    }
}

export default Coder;