import eventManager from "./EventManager";

class Client {

    ready = false;
    authorized = false;
    response = {};
    villages = {};
    activeVillageName = "";
    activeVillageId = window.localStorage.getItem("vid") || 0;
    uid = window.localStorage.getItem("uid") || "";
    key = window.localStorage.getItem("key") || "";
    serverNumber = window.localStorage.getItem("server") || 0;

    constructor() {
        eventManager.create("ready");
    }

    /** @param {{uid:String, server:Number, authorized:Boolean, activeVillageId:Number}} response */
    join(response) {
        this.uid = response.uid;
        this.serverNumber = response.server;
        this.authorized = response.authorized === true;
        this.activeVillageId = response.activeVillageId;
        window.localStorage.setItem("uid", response.uid);
        window.localStorage.setItem("vid", response.activeVillageId);
        this.ready = true;
    }
    /** @param {{ server: Number, authorized: Boolean, key: String, activeVillageId: Number }} response */
    login(response) {
        this.serverNumber = response.server;
        this.authorized = response.authorized === true;
        this.key = response.key;
        this.activeVillageId = response.activeVillageId;
        window.localStorage.setItem("server", response.server);
        window.localStorage.setItem("key", response.key);
        window.localStorage.setItem("vid", response.activeVillageId);
    }
    setState(authorized = false, uid = "", key = "") {
        this.authorized = authorized === true;
        this.uid = uid;
        this.key = key;
        window.localStorage.setItem("uid", uid);
        window.localStorage.setItem("key", key);
    }
}

const client = new Client();

export default client;